import React from "react";
import Tab from "../components/tab/tab";
import ProductCard from "../components/card/NumberCard";
import Ad from '../components/ad'

const TabContent = ({ items }) => {
    return (
        <div className="row touchable">
            {items.map((item, i) => (
                <div key={i} className="col-sm-4">
                    <ProductCard data={item} />
                </div>
            ))}
        </div>
    );
};

const Component = ({ pageContext }) => {
    const { items,  nextLink, prevLink } = pageContext;
    return (
        <section className="flash__sell pt-25 pb-40 grey-bg-2">
            <Tab
                title={`Pick A Number`}
                titleBold={``}
                tabObject={{
                    groupbuy: <TabContent items={items} />,
                }}
            />
            <Ad />
            <div className="container">

                <a href={prevLink} className={`btn btn-primary ${prevLink || 'disabled'}`}>Prev</a>
                <a href={nextLink} className={`btn btn-primary float-end ${nextLink || 'disabled'}`}>Next</a>
                

            </div>

        </section>

    );
};

export default Component;
